import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import helper from '../../../helper/tokenHelper';
import { ITokenBase } from '../../../model/tokenModel';
import { ICurrencyInput } from '../../../model/swapModel';
import CurrencySellectBtn from './CurrencySellectBtn/CurrencySellectBtn';
import CurrencyAmountInput from './CurrencyAmountInput/CurrencyAmountInput';
import ModalSelectToken from './../components/ModalSelectToken/ModalSelectToken';
import style from './CurrencyInput.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps extends ICurrencyInput {
  isDisable?: boolean;
  onChange: (v: ICurrencyInput) => void;
}
interface IOwnState {
  isOpenModal: boolean;
}

class CurrencyInput extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    balance: '0',
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      isOpenModal: false,
    };

    this.onCurrencySellectBtnClick = this.onCurrencySellectBtnClick.bind(this);
    this.onAmountChange = this.onAmountChange.bind(this);
    this.onModalSelectTokenToggle = this.onModalSelectTokenToggle.bind(this);
    this.onSelectToken = this.onSelectToken.bind(this);
  }

  onCurrencySellectBtnClick() {
    this.setState({ isOpenModal: true });
  }

  onAmountChange(value: string) {
    const { onChange } = this.props;

    onChange && onChange({ value } as ICurrencyInput);
  }

  onModalSelectTokenToggle() {
    this.setState({ isOpenModal: false });
  }

  onSelectToken(token: ITokenBase) {
    const { onChange } = this.props;

    onChange && onChange(token as ICurrencyInput);
  }

  render(): ReactElement {
    const {
      isDisable,
      symbol,
      address,
      logoURI,
      value,
      balance,
      price,
      liquidity,
    } = this.props;
    const { isOpenModal } = this.state;

    return (
      <div className={style.wrap}>
        <div className={style.title}>
          <CurrencySellectBtn
            symbol={symbol}
            address={address}
            logoURI={logoURI}
            onClick={this.onCurrencySellectBtnClick}
          />
          <span className={'fk-swap-text-info ' + style.balance}>
            Balance:{' '}
            {helper.convertBalanceToInternationalCurrencySystem(
              Number(balance)
            )}
          </span>
        </div>
        <CurrencyAmountInput
          isDisable={isDisable}
          address={address}
          balance={balance}
          price={price}
          liquidity={liquidity}
          value={value}
          onChange={this.onAmountChange}
        />
        <ModalSelectToken
          isOpen={isOpenModal}
          address={address}
          onToggle={this.onModalSelectTokenToggle}
          onSelect={this.onSelectToken}
        />
      </div>
    );
  }
}

export default CurrencyInput;
