import { PureComponent, ReactElement } from 'react';
import style from './RouteSwap.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  route: string;
}
interface IOwnState {}

class RouteSwap extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};
  }

  render(): ReactElement {
    const { route } = this.props;

    return (
      <div
        className={
          'd-flex justify-content-between align-items-center fk-swap-text-info ' +
          style.wrap
        }
      >
        <div>Route</div>
        <div>{route}</div>
      </div>
    );
  }
}

export default RouteSwap;
