import { PureComponent, ReactElement } from 'react';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../../../../config/tokenBep20Config';
import helper from '../../../../helper/tokenHelper';
import style from './CurrencyAmountInput.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isDisable: boolean;
  address: string;
  balance: string;
  price: string;
  liquidity: string;
  value: string;
  onChange: (v: string) => void;
}
interface IOwnState {}

class CurrencyAmountInput extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isDisable: false,
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};

    this.onChange = this.onChange.bind(this);
    this.onMaxValue = this.onMaxValue.bind(this);
  }

  private onChange(e: { target: { value: string } }) {
    const { onChange } = this.props;
    const value: string = e.target.value;
    const re = /^[0-9]*[.,]?[0-9]*$/;

    if (value.trim() === '') {
      onChange('');
    } else if (re.test(value)) {
      onChange(value.replace(',', '.'));
    }
  }

  private onMaxValue() {
    const { balance, onChange } = this.props;

    balance && onChange && onChange(balance);
  }

  private checkShowPrice(): boolean {
    return true;
    return ![
      BUSD_BEP20.address,
      USDT_BEP20.address,
      USDC_BEP20.address,
    ].includes(this.props.address);
  }

  private checkShowLiquidity(): boolean {
    return true;
    return ![
      BUSD_BEP20.address,
      USDT_BEP20.address,
      USDC_BEP20.address,
      BNB_BEP20.address,
    ].includes(this.props.address);
  }

  render(): ReactElement {
    const { isDisable, value, price, liquidity } = this.props;
    return (
      <label className={style.wrap}>
        <input
          autoFocus
          className={style.input}
          placeholder='0.0'
          disabled={isDisable}
          value={value}
          onChange={this.onChange}
        />
        <div className={style.info}>
          <span className='fk-swap-text-info'>
            {this.checkShowPrice() && `$${helper.roundCurrency(Number(price))}`}
            {this.checkShowLiquidity() &&
              ` ☞ ${helper.convertToInternationalCurrencySystem(
                Number(liquidity)
              )}`}
          </span>
          {!isDisable && (
            <button className={style.max} onClick={this.onMaxValue}>
              MAX
            </button>
          )}
        </div>
      </label>
    );
  }
}

export default CurrencyAmountInput;
