import { PureComponent, ReactElement, ChangeEvent } from 'react';
import { connect } from 'react-redux';
import { Modal } from 'reactstrap';
import { Scrollbars } from 'react-custom-scrollbars-2';
import { ITokenBase } from '../../../../model/tokenModel';
import { IStoreState } from '../../../../store/config/store';
import { mappingModalSelectToken } from '../../../../service/swapMappingService';
import logoBep20 from '../../../../asset/logo/bep20.png';
import style from './ModalSelectToken.module.scss';

interface IStoreProps {
  tokens: ITokenBase[];
}
interface IHOCProps {}
interface IOwnProps {
  isOpen: boolean;
  address: string;
  onToggle: () => void;
  onSelect: (v: ITokenBase) => void;
}
interface IOwnState {
  filterText: string;
}

class ModalSelectToken extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = { filterText: '' };

    this.onToggle = this.onToggle.bind(this);
    this.onChangeFilter = this.onChangeFilter.bind(this);
  }

  private onToggle() {
    this.props.onToggle();
  }

  private onChangeFilter(e: ChangeEvent<HTMLInputElement>) {
    this.setState({
      filterText: e.target.value,
    });
  }

  private onSelectToken(token: ITokenBase) {
    this.props.onSelect(token);
    this.onToggle();
  }

  private filterTokens(tokens: ITokenBase[]): ITokenBase[] {
    const { address } = this.props;
    const { filterText } = this.state;

    const filteredTokens: ITokenBase[] = tokens.filter(
      (token: ITokenBase): boolean => {
        return (
          token.address !== address &&
          (token.address.toLowerCase() === filterText.toLowerCase() ||
            (token.symbol || token.name)
              .toLowerCase()
              .includes(filterText.toLowerCase()) ||
            token.name.toLowerCase().includes(filterText.toLowerCase()))
        );
      }
    );

    return filteredTokens;
  }

  render(): ReactElement {
    const { tokens, isOpen } = this.props;

    return (
      <Modal
        centered
        className='fk-modal'
        isOpen={isOpen}
        fade={false}
        animation='false'
        toggle={this.onToggle}
      >
        <div className='d-flex justify-content-between mb-2 p-4 header'>
          <span className='header-title'>Select a Token</span>
          <button className='button-close' onClick={this.onToggle}>
            <i className='icon-close bi bi-x-lg'></i>
          </button>
        </div>
        <div className='body'>
          <div className='p-4'>
            <input
              autoFocus
              className='fk-input fk-input-lg'
              type='text'
              placeholder='Search name or paste address'
              onChange={this.onChangeFilter}
            />
          </div>
          <Scrollbars style={{ height: 400 }}>
            {this.filterTokens(tokens).map((token: ITokenBase) => (
              <div
                key={token.address}
                className={style.item}
                onClick={() => this.onSelectToken(token)}
              >
                <img
                  className={style.logo}
                  alt={token.name}
                  src={token.logoURI || logoBep20}
                  title={token.address}
                />
                <div>
                  <div className={style.symbol}>{token.symbol}</div>
                  <div className={style.name}>{token.name}</div>
                </div>
              </div>
            ))}
          </Scrollbars>
        </div>
        <div className='d-flex justify-content-end p-4 footer'>
          <span className={style.note}>Total: {tokens.length}</span>
        </div>
      </Modal>
    );
  }
}

const mapState = (state: IStoreState): IStoreProps => ({
  tokens: mappingModalSelectToken(state.tokens),
});

export default connect(mapState)(ModalSelectToken);
