import { PureComponent, ReactElement } from 'react';
import style from './ButtonSwapPosition.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  onClick?: Function;
}
interface IOwnState {}

class ButtonSwapPosition extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};
    this.onClick = this.onClick.bind(this);
  }

  private onClick() {
    this.props.onClick && this.props.onClick();
  }

  render(): ReactElement {
    return (
      <button className={style.btn} onClick={this.onClick}>
        <i className='bi bi-arrow-down-up'></i>
      </button>
    );
  }
}

export default ButtonSwapPosition;
