import { PureComponent, ReactElement } from 'react';
import { TXN_BURN_MAX } from '../../../config/appSwapConfig';
import swapHelper from '../../../helper/swapHelper';
import Checkbox from '../controls/Checkbox/Checkbox';
import Input from '../controls/InputInt/InputInt';
import style from './BurnToken.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  burnPredict: number;
  onChange: (v: { isBurnActive: boolean; burn: number }) => void;
}
interface IOwnState {
  isBurnActive: boolean;
  burn: number;
}

class BurnToken extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      isBurnActive: false,
      burn: props.burnPredict,
    };

    this.onChkChange = this.onChkChange.bind(this);
    this.onInputChange = this.onInputChange.bind(this);
  }

  private onChkChange(value: boolean) {
    this.setState(
      {
        isBurnActive: value,
      },
      () => {
        this.props.onChange(this.state);
      }
    );
  }

  private onInputChange(value: number) {
    this.setState(
      {
        burn: value,
      },
      () => {
        this.props.onChange(this.state);
      }
    );
  }

  render(): ReactElement {
    const { burnPredict } = this.props;
    const { isBurnActive, burn } = this.state;

    return (
      <div
        className={
          'd-flex justify-content-between align-items-center fk-swap-text-info ' +
          style.wrap
        }
      >
        <div>Burn</div>
        <div
          className={
            'd-flex align-items-center ' +
            swapHelper.getClassWarningBurn(burnPredict)
          }
        >
          {!isBurnActive && (
            <span>{burnPredict === -1 ? '?' : `(${burnPredict}%)`}</span>
          )}
          {isBurnActive && (
            <Input
              isAutoFocus={true}
              classInput={swapHelper.getClassWarningBurn(burn)}
              title='%'
              value={burn === -1 ? 1 : burn}
              maxValue={TXN_BURN_MAX}
              onChange={this.onInputChange}
            />
          )}
          <div className={style.checkbox}>
            <Checkbox isCheck={isBurnActive} onChkChange={this.onChkChange} />
          </div>
        </div>
      </div>
    );
  }
}

export default BurnToken;
