import { PureComponent, ReactElement, Fragment } from 'react';
import logoBep20 from '../../../../asset/logo/bep20.png';
import style from './CurrencySellectBtn.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  symbol: string;
  address: string;
  logoURI: string;
  onClick: () => void;
}
interface IOwnState {}

class CurrencySellectBtn extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};
  }

  render(): ReactElement {
    const { symbol, address, logoURI, onClick } = this.props;

    return (
      <Fragment>
        <button className={style.selectBtn} onClick={onClick}>
          <img
            className={style.logo}
            src={logoURI || logoBep20}
            alt={address}
          />
          <span className={style.symbol}>{symbol}</span>
          <i className={`${style.iconDropdown} bi bi-chevron-compact-down`}></i>
        </button>
      </Fragment>
    );
  }
}

export default CurrencySellectBtn;
