import { PureComponent, ReactElement, Fragment } from 'react';
import _ from 'lodash';
import { connect } from 'react-redux';
import { Helmet, HelmetProvider } from 'react-helmet-async';
import { Row, Col } from 'reactstrap';
import { BUSD_BEP20, BNB_BEP20 } from '../../config/tokenBep20Config';
import { IRoutedProps, withRouter } from '../../lib/HOC';
import { IStoreState } from '../../store/config/store';
import { ITokenBase } from '../../model/tokenModel';
import { ICurrencyInput } from '../../model/swapModel';
import { getTokenInfos } from '../../service/tokenInfoService';
import { mappingModalSelectToken } from '../../service/swapMappingService';
import { getPools } from '../../service/tokenPoolService';
import CurrencyInput from './CurrencyInput/CurrencyInput';
import PriceImpact from './PriceImpact/PriceImpact';
import RouteSwap from './RouteSwap/RouteSwap';
import BurnToken from './BurnToken/BurnToken';
import GasSlip from './GasSlip/GasSlip';
import StatusMessage from './StatusMessage/StatusMessage';
import ButtonSwapPosition from './ButtonSwapPosition/ButtonSwapPosition';
import ButtonAction from './ButtonAction/ButtonAction';
import style from './Swap.module.scss';

interface IStoreProps {
  tokens: ITokenBase[];
}
interface IHOCProps extends IRoutedProps {}
interface IOwnProps {}
interface IOwnState {
  input: ICurrencyInput;
  output: ICurrencyInput;
}

class Swap extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      input: {
        ...(BUSD_BEP20 as ICurrencyInput),
        balance: '100000000000',
        price: '0.0000000061346',
        liquidity: '300',
        value: '',
      },
      output: {
        ...(BNB_BEP20 as ICurrencyInput),
        balance: '1215.123456789',
        price: '0.0613',
        liquidity: '3000000',
        value: '',
      },
    };

    this.onCurrencyInputChange = this.onCurrencyInputChange.bind(this);
    this.onCurrencyOutputChange = this.onCurrencyOutputChange.bind(this);
    this.onBurnSettingChange = this.onBurnSettingChange.bind(this);
    this.onGasSlipSettingChange = this.onGasSlipSettingChange.bind(this);
    this.onSwapPosition = this.onSwapPosition.bind(this);
    this.onSwapToken = this.onSwapToken.bind(this);
  }

  async componentDidMount() {
    await getTokenInfos();

    const { params, tokens } = this.props;

    if (params.address) {
      const tokenOutput: ITokenBase = _.find(tokens, {
        address: params.address.toLowerCase(),
      }) as ITokenBase;

      if (tokenOutput) {
        this.setState((state: IOwnState) => ({
          output: {
            ...state.output,
            ...tokenOutput,
          },
        }));
      }
    }
  }

  private onCurrencyInputChange(change: ICurrencyInput) {
    this.setState((state: IOwnState) => ({
      input: {
        ...state.input,
        ...change,
      },
    }));
    console.log('onCurrencyInputChange: ', change);
  }

  private onCurrencyOutputChange(change: ICurrencyInput) {
    this.setState((state: IOwnState) => ({
      output: {
        ...state.output,
        ...change,
      },
    }));
    console.log('onCurrencyOutputChange: ', change);
  }

  private onBurnSettingChange(value: { isBurnActive: boolean; burn: number }) {
    console.log('onBurnSettingChange: ', value);
  }

  private onGasSlipSettingChange(value: { gasPrice: number; slip: number }) {
    console.log('onGasSlipSettingChange: ', value);
  }

  private onSwapPosition() {
    this.setState((state: IOwnState) => ({
      input: state.output,
      output: state.input,
    }));
  }

  private onSwapToken() {
    console.log('onSwapToken');
  }

  render(): ReactElement {
    const { input, output } = this.state;

    return (
      <Fragment>
        <HelmetProvider>
          <Helmet>
            <title>Swap</title>
          </Helmet>
        </HelmetProvider>
        <Row>
          <Col
            sm={{
              offset: 2,
              size: 8,
            }}
            md={{
              offset: 3,
              size: 6,
            }}
            lg={{
              offset: 4,
              size: 4,
            }}
          >
            <div className='fk-container p-3 p-md-4'>
              <div className='mb-4'>
                <CurrencyInput
                  symbol={input.symbol}
                  address={input.address}
                  logoURI={input.logoURI}
                  balance={input.balance}
                  price={input.price}
                  liquidity={input.liquidity}
                  value={input.value}
                  onChange={this.onCurrencyInputChange}
                />
                <div className='text-center'>
                  <ButtonSwapPosition onClick={this.onSwapPosition} />
                </div>
                <CurrencyInput
                  isDisable={true}
                  symbol={output.symbol}
                  address={output.address}
                  logoURI={output.logoURI}
                  balance={output.balance}
                  price={output.price}
                  value={output.value}
                  liquidity={output.liquidity}
                  onChange={this.onCurrencyOutputChange}
                />
              </div>
              <div className='mb-5'>
                <div className='mb-2'>
                  <PriceImpact percent={5} price={0.000007} />
                </div>
                <div className='mb-2'>
                  <RouteSwap route={'BUSD > BNB > DOT'} />
                </div>
                <div className='mb-2'>
                  <BurnToken
                    burnPredict={6}
                    onChange={this.onBurnSettingChange}
                  />
                </div>
                <div className='mb-2'>
                  <GasSlip onChange={this.onGasSlipSettingChange} />
                </div>
              </div>
              {/* <div className='mb-3'>
                <StatusMessage isSuccess={true} message={'Success!'} />
              </div> */}
              <ButtonAction title='Swap' onClick={this.onSwapToken} />
            </div>
          </Col>
        </Row>
      </Fragment>
    );
  }
}

const mapState = (state: IStoreState): IStoreProps => ({
  tokens: mappingModalSelectToken(state.tokens),
});

export default connect(mapState)(withRouter(Swap));
