import { PureComponent, ReactElement } from 'react';
import style from './ButtonAction.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  title: string;
  isDisable?: boolean;
  onClick?: Function;
}
interface IOwnState {}

class ButtonAction extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};
    this.onClick = this.onClick.bind(this);
  }

  private onClick() {
    this.props.onClick && this.props.onClick();
  }

  render(): ReactElement {
    const { title, isDisable } = this.props;

    return (
      <button className={style.btn} disabled={isDisable} onClick={this.onClick}>
        {title}
      </button>
    );
  }
}

export default ButtonAction;
