import { PureComponent, ReactElement } from 'react';
import style from './Checkbox.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  rightTitle?: string;
  leftTitle?: string;
  isCheck?: boolean;
  onChkChange?: Function;
}
interface IOwnState {}

class Checkbox extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};

    this.onChange = this.onChange.bind(this);
  }

  onChange() {
    const { isCheck, onChkChange } = this.props;

    onChkChange && onChkChange(!isCheck);
  }

  render(): ReactElement {
    const { rightTitle, leftTitle, isCheck } = this.props;

    return (
      <label
        className={
          'd-inline-flex align-items-center fk-swap-text-info ' + style.checkbox
        }
      >
        {leftTitle && <span className={style.leftTitle}>{leftTitle}</span>}
        <input
          className={style.checkcontrol}
          type='checkbox'
          checked={isCheck}
          onChange={this.onChange}
        />
        <span className={style.checkmark}></span>
        {rightTitle && <span className={style.rightTitle}>{rightTitle}</span>}
      </label>
    );
  }
}

export default Checkbox;
