import _ from 'lodash';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../config/tokenBep20Config';
import tokenHelper from '../helper/tokenHelper';
import { ITokenBase } from '../model/tokenModel';

export function mappingModalSelectToken(tokens: ITokenBase[]): ITokenBase[] {
  const mappedModalSelectToken: ITokenBase[] = _.chain(tokens)
    .map(
      (token: ITokenBase): ITokenBase => ({
        symbol: token.symbol,
        name: `${
          _.isNil(token.burn)
            ? token.name
            : '*[' +
              tokenHelper.buildBurnPercent(token.burn) +
              '] ' +
              token.name
        }`,
        address: token.address,
        decimals: token.decimals,
        logoURI: token.logoURI,
      })
    )
    .sortBy(['symbol'])
    .value();

  return [
    BUSD_BEP20,
    USDT_BEP20,
    USDC_BEP20,
    BNB_BEP20,
    ...mappedModalSelectToken,
  ];
}
