import { PureComponent, ReactElement } from 'react';
import style from './InputInt.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isAutoFocus: boolean;
  classInput?: string;
  title?: string;
  value?: number;
  maxValue: number;
  onChange: (v: number) => void;
}
interface IOwnState {}

class InputInt extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isAutoFocus: false,
    maxValue: 99,
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};

    this.onChange = this.onChange.bind(this);
  }

  onChange(e: { target: { value: string } }) {
    const { maxValue, onChange } = this.props;
    const value: string = e.target.value;
    const re = /^[0-9]*$/;

    if (onChange) {
      if (value.trim() === '') {
        onChange(0);
      } else if (re.test(value)) {
        onChange(Math.min(parseInt(value), maxValue));
      }
    }
  }

  render(): ReactElement {
    const { isAutoFocus, classInput, title, value } = this.props;

    return (
      <label className='d-inline-flex align-items-center fk-swap-text-info'>
        <input
          autoFocus={isAutoFocus}
          className={(classInput ? classInput + ' ' : '') + style.input}
          value={value}
          onChange={this.onChange}
        />
        {title && (
          <span className={(classInput ? classInput + ' ' : '') + style.title}>
            {title}
          </span>
        )}
      </label>
    );
  }
}

export default InputInt;
